import React from 'react'
import Logo from "../img/logo.png"
import About from "./About";
import Menu from "./Menu";
import Prices from "./Prices";
import Gallery from "./Gallery";
import Footer from "./Footer";
import { NavLink } from "react-router-dom"

const Header = () => {
    return (
        <>
        <div className="header">
            <div className="container">
                <div className="navbar">
                    <div className="logo">
                         <NavLink to="/"><img src={Logo}/></NavLink>
                
                    </div>
                <div><nav>
                <ul id="MenuItems">
                        <li><NavLink to="/">HOME</NavLink></li>
                        <li><NavLink to="/aboutpage">ABOUT US </NavLink></li>
                        <li><NavLink to="/productpage">PRODUCT</NavLink></li>
                        <li> <div className="callnow"><a href="tel:+91-9560125641">Call Now</a></div></li>
                    </ul>
                </nav></div>
                </div>
                <div className="row">              
                    <div className="colum-4">
                        <h3>CCTV'S INSTALLION</h3>
                        <h1>OWL EYE</h1>
                        <p>We are Proving Best and Fastest  servic. We are here for you.Proving many type of cctv camera installation.</p>
                    </div>
                </div>
            </div>
            <div className="fixed"><a href="tel:+91-9560125641">Call Now</a></div>           
        </div>
        <About/>
        <Prices/>
        <Menu/>
        <Gallery/>
        <Footer/>
        </>
    )
}

export default Header
