import React from 'react';
import { NavLink } from 'react-router-dom';
import Pizza from "../img/pizza1.jpg"

function Menu() {
    return (
        <div className="about" id="menu">
                
                    <div className="about-b img-a">
                        <div className="about__img">
                            <img src={Pizza} alt=""Pizza/>
                        </div>
                    </div>
                    <div className="about-a" >
                    <h3>The CCTV Service's</h3>
                     <h2>All tips of camera serivce's</h2>
                     <p>NETWORK CAMERA | PTZ CAMERA | HD ANALOG CAMERA | NVR | DVR | ENCODER | DECODER | CONTROLER | COAXIAL TRANSMISSION</p>
                    <div className="link__btn">
                        <NavLink to="/aboutpage"> VIEW MORE</NavLink>
                    </div>
                    </div>
                </div>
        
    )
}

export default Menu
