import React from 'react'

function Item(props) {
    return (
        <div className="col4 item">
            <a href="tel:+91-9560125641">
            <img src={props.pimg} alt="pizza"/>
           <div className="hdg"><h4> {props.heading } </h4></div>
            <div class="rating">
                <i class="fa fa-star"></i>
                <i class="fa fa-star"></i>
                <i class="fa fa-star"></i>
                <i class="fa fa-star"></i>
                <i class="fa fa-star"></i>
            </div>
            <p>{props.price}</p>
            </a>
        </div>
    )
}

export default Item;
